import * as React from 'react';
import styles from './CustomTilesViewCard.module.scss';
import { ICustomTilesViewWpProps } from './ICustomTilesViewWpProps';
import { escape } from '@microsoft/sp-lodash-subset';
import {SPHttpClient, HttpClient, SPHttpClientResponse, HttpClientResponse} from '@microsoft/sp-http'
import {IListItem} from './IListItem';
import { ITilesviewState } from './ITilesViewState';
import {SPComponentLoader} from '@microsoft/sp-loader';
//require("./tiles.style.css");
require("./style.css");
export default class CustomTilesViewWp extends React.Component<ICustomTilesViewWpProps, ITilesviewState> {
  topItems : Array<IListItem>  ;
  domainString : string;
  constructor(props: ICustomTilesViewWpProps, state: ITilesviewState)
  {
    super(props);
    SPComponentLoader.loadCss('https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css');	 
    SPComponentLoader.loadCss('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.5.0/css/font-awesome.css');
    SPComponentLoader.loadCss('https://wallerotemp.sharepoint.com/SPFxAssets/stylegrd.css');
	//<link href="css/style.css" rel="stylesheet" type="text/css"> 
     this.state = {
      currentState: "initial",
      items : Array<IListItem>()
    }
  }
  componentWillMount()
  {
    try
    {
      this.domainString = (window.location.href.toLowerCase().split("domain=")[1]).trim();
      this.domainString = this.domainString ? this.domainString :"";
    }
    catch(error)
    {
      console.log(error);
    }
  }
  componentDidMount()
  {
    try
    {
        this.getListItems().then(item =>
          {
            debugger;        
            this.setState({
              currentState:"data"
            });
          }
        );
    }
    catch(error)
    {
      console.log(error);
    }
  }
   getListItems()
  { 
    try
    {
    debugger;
    return new Promise((resolve, reject) =>
    {   
      //this.props.spHttpClient.get(`${this.props.siteurl}/_api/web/lists/getbytitle('Apps_Infrastructure')/Items?$filter=Domain/ID eq '`+this.domainString+`'`,       
      this.props.spHttpClient.get(`${this.props.siteurl}/_api/web/lists/getbytitle('Apps_Infrastructure')/items?$select=Play,Sales_Conversation,Market_Opportunities,Domain/Title,Domain/ID&$expand=Domain/Id&$filter=Domain/Id%20eq%20`+this.domainString,       
      SPHttpClient.configurations.v1,{headers: {'Accept': 'application/json;odata=nometadata','odata-version': ''}})
      .then((response: SPHttpClientResponse): Promise<{ value: IListItem[] }> => {
        return response.json();
      })
      .then((response: { value: IListItem[] }): void => {   
        console.log(response.value);  
        this.topItems = response.value;   
        resolve(response.value);
      })
    });
    }
    catch(error)
    {
      console.log(error);
    }
  }
  public render():JSX.Element {
    var i=0;
    try
    {
    var that = this;
    if(this.topItems)
    {
      const items = this.topItems;
      return (
        <code>  
           {<div className="container">
            <header id="header_outer">
				      <h2>Lorem Ipsum</h2>		 
			      </header>
            <section id="Portfolio" className="content"><h1>Apps and Infrastructure</h1></section>
            <div className="row app-headings hidden-xs hidden-sm">
				      <div className="col-sm-12 col-md-6 col-lg-6">
					      PLAYS
				      </div>
				      <div className="col-sm-6 col-md-3 col-lg-3">
					      SALES CONVERSATION
				      </div>
				      <div className="col-sm-6 col-md-3 col-lg-3">
					      MARKET OPPORTUNITY
				      </div>
			      </div>
            </div>}
           {items.map((item) =>
            <div className="container">            
              <div className='row app-rows-one'>
                <div className="col-sm-12 col-md-6 col-lg-6 h-v">
                  <div className="img-container">
                    <a href={item.Play.Url}><img className="image"src={item.Play.Url}/></a>
                  </div>
                  <div className="overlay-o">
                    <div className="text-hover">
                      <p><a href={item.Play.Url}>{item.Play.Description}</a></p>
                    </div>   
                  </div>      
                </div>
                <div className="col-sm-6 col-md-3 col-lg-3">
                  <div left-charts>
                    <img className="fa fa-line-chart imagesize" src={item.Sales_Conversation.Url}/>
                    <div className="chart-text">
                      <p>{item.Sales_Conversation.Description}</p>
                    </div>
                  </div>         
                </div>
                <div className="col-sm-6 col-md-3 col-lg-3">
                  <div left-charts>
                    <img className="fa fa-line-chart imagesize" src={item.Market_Opportunities.Url}/>
                    <div className="chart-text">
                      <p>{item.Market_Opportunities.Description}</p>
                    </div>
                  </div>         
                </div>  
              </div> 
            </div>      
           )}   
        </code> 
      )
     } 
     else
     {
       return (<div>Data Not Available</div>)
     }
    }   
    catch(error)
    {
      console.log(error);
    }
  }
}
